.investment-card {
  width: 300px;
  padding: 15px;
  background-color: #f8f9fa;
  border: 1px solid var(--soft-color);
  border-radius: 10px;
  text-align: center;
  margin-top: 15px;
  margin-right: 20px;
  position: relative;
}

.investment-card__logo {
  position: absolute;
  top: -20px;
  left: -20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid var(--soft-color); /* Optional: Add a border to the circle */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); /* Optional: Add a slight shadow */
}

.investment-card__logo-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

.investment-card__image {
  width: 100%;
  height: auto;
  cursor: pointer;
}

.investment-card__like {
  position: absolute;
  top: 25px;
  right: 25px;
  z-index: 10;
}

.like-button {
  background-color: white;
  border: none;
  border-radius: 10px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  cursor: pointer;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.like-button svg {
  color: var(--red-color);
  width: 20px;
  height: 20px;
}

.like-button.liked svg {
  color: var(--red-color);
}

.investment-card__description {
  font-size: 1rem;
  color: #777;
  margin-top: 0px;
}

.investment-card__info {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  color: #555;
}

.investment-card__info-item {
  text-align: center;
  flex: 1;
  position: relative;
  margin-bottom: 0px;
}

.investment-card__info-item:not(:last-child)::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  height: 60%;
  border-right: 1px solid #ccc;
}
.investment-card__about {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  max-height: 4.5em; /* Approximately 3 lines of text */
  line-height: 1.5em;
}
.investment-card__info-value {
  font-size: 0.9rem;
  font-weight: bold;
}

.investment-card__info-label {
  font-size: 0.9rem;
  color: #777;
  margin-top: 2px;
}
.investment-card__title {
  color: var(--txtMain-color);
}

.investment-card__progress {
  margin-top: 10px;
}
